:host {
  display: block;
  p {
    margin: 0 0 10px;
  }
  ul.dropdown-menu {
    width: 100%;

    padding: 0;
    margin: 0;
    // background: $color-white;
    box-shadow: 0 0 4px 0 rgba(0,0,0,0.20);
    border-radius: 0 0 2px 2px;    
    // margin-top: 6px;
    // // margin-left: -1px;
    // padding: 0;
    // max-height: 200px;
    overflow-y: auto;
    min-width: 100%;
    li {
      cursor: pointer;
      outline: none;
      // a {
        outline: none;
        // height: 40px;
        padding: 3px 10px;       
        overflow: hidden;
        .file-option-item {
          display: flex;
          align-items: center;
          > div {
            padding: 8px;
            width: calc(100% - 20px);
          }
          .title {
            line-height: 16px;
            // color: $color-black;
            margin-bottom: 5px;
          }
          .desc {
            line-height: 16px;
            // color: $color-gray-dove;
            white-space: break-spaces;
          }
        }
        &.multi-choice {
          line-height: 40px;
          padding-top: 1px;
          .checkbox-survey123>label {
            width: 100%;
          }
        }
        label {
          cursor: inherit;
          span {
            line-height: 32px;
            max-width: 100%;
            // font-weight: $font-weight4;
            overflow: hidden;
            text-overflow: ellipsis;
            display: inline-block;
            &:focus {
              outline: none;
            }
          }
        }
        &:hover, &:focus {
          // background:  $color-blue-spindle;
          // color: $color-black;
          // font-weight: $font-weight4;
          background-color: var(--calcite-color-foreground-2);
          color: var(--calcite-color-text-1);
          text-decoration-line: none;
          --tw-shadow: 0 0 #0000;
          --tw-shadow-colored: 0 0 #0000;
          box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
          span {
            // font-weight: $font-weight4;
          }
        }
      // }
      &.active{
        // background:  $color-blue-spindle;
        // color: $color-black ;
      }
      &.active >a, >a:hover, >a:focus {
        ::ng-deep svg {
          path:nth-child(1){
            fill: #95ABB3
          }
          path:nth-child(2){
            fill: #703B00
          }
          path:nth-child(3){
            fill: #697D94
          }
        }
      }
      &.deactive {
        cursor: not-allowed;
        a {
          &:hover, &:focus {
            // background:  $color-black-haze;
          }
        }
        &:focus {
          // outline-color: $color-black-haze;
        }
      }

    }
    li:focus {
      outline: none;
    }
  }

}

