import { Component, Host, Prop, EventEmitter, State, Event, h } from '@stencil/core';
import mergeContinuousIcon from './assets/svg/Merge_Continuous.svg';
import splitIcon from './assets/svg/Split.svg';
import mergeNextPageIcon from './assets/svg/Merge_Next_Page.svg';
import { TranslateService } from '../../../services/translate.service';
import { StateService } from '../../../services/state.service';
@Component({
  tag: 'file-option-chooser',
  styleUrl: 'file-option-chooser.scss',
  shadow: true,
})
export class FileOptionChooser {

  @Prop({reflect: true }) fileOption: 'none' | 'nextPage' | 'continuous' = 'none';
  @Event() fileOptionChange: EventEmitter<any>;

  private ele: any;
  @State() translator: any;
  @State() langReport: any;
  private state: StateService = StateService.getService();

  componentWillLoad() {
    return Promise.resolve(true)
    // .then(() => {
    //   TranslateService
    //   return TranslateService.getService().getTranslate();
    // })
    .then(() => {
      const res = TranslateService.getService().getTranslateSync();
      this.translator = res;
      this.langReport = res?.customPrint;
      this.state.subscribe('locale-data-changed', (data) => {
        this.translator = data;
        this.langReport = data?.customPrint;
      });
    })
  }

  /**
   * selet item
   * @param type 
   */
  public selectItem(type: 'none' | 'nextPage' | 'continuous') {
    this.fileOption = type;
    this.fileOptionChange.emit(this.fileOption);
    this.ele.open = false;
    if (this.fileOption) {
      this.ele.value = {
        split: this.translator?.customPrint?.outputModeSplit,
        nextPage: this.translator?.customPrint?.outputModeMergeNextPage,
        continuous: this.translator?.customPrint?.outputModeMergeContinuous
      }[this.fileOption];
    } else {
      this.ele.value = null;
    }
    // this.ele.shadowRoot.querySelector('input').value = this.fileOption; // todo: convert to the label
  }

  private svgs = {
    mergeContinuousIcon: mergeContinuousIcon, // getAssetPath('./assets/svg/Merge_Continuous.svg'),
    splitIcon: splitIcon, // getAssetPath('./assets/svg/Split.svg'),
    mergeNextPageIcon: mergeNextPageIcon //getAssetPath('./assets/svg/Merge_Next_Page.svg')
  };

  render() {
    return (
      <Host>
        <calcite-label>
          {this.translator?.customPrint?.outputMode}
          {/* todo: if the selection count is 0 or 1, the dropdown should be disabled */}
          <calcite-combobox ref={(el) => (this.ele = el)} clearDisabled="true"  placeholder={this.translator?.customPrint?.outputMode} selection-mode="single-persist">
             {/* the calcite-combobox-item is just a placeholder, it's hidden */}
            <calcite-combobox-item value={this.translator?.customPrint?.outputModeSplit} style={{display: 'none'}} selected={this.fileOption==='none'} text-label={this.translator?.customPrint?.outputModeSplit}></calcite-combobox-item>
            <calcite-combobox-item value={this.translator?.customPrint?.outputModeMergeNextPage} style={{display: 'none'}} selected={this.fileOption==='nextPage'} text-label={this.translator?.customPrint?.outputModeMergeNextPage}></calcite-combobox-item>
            <calcite-combobox-item value={this.translator?.customPrint?.outputModeMergeContinuous} style={{display: 'none'}} selected={this.fileOption==='continuous'} text-label={this.translator?.customPrint?.outputModeMergeContinuous}></calcite-combobox-item>
            <ul class="dropdown-menu" role="menu">
              <li onClick={() => {this.selectItem('none')}}>
              {/* <a class="option-wrapper"> */}
                <div class="file-option-item">
                  <img class="bubble__avatar-thumbnail" src={this.svgs.splitIcon}></img>
                  <div>
                    <p class='title'>{this.translator?.customPrint?.outputModeSplit}</p>
                    <p class='desc'>
                      {this.translator?.customPrint?.outputModeSplitDesc}
                    </p>
                  </div>
                </div>
              {/* </a> */}
            </li>
            <li onClick={() => {this.selectItem('nextPage')}}>
              {/* <a class="option-wrapper"> */}
                <div class="file-option-item">
                  <img class="bubble__avatar-thumbnail" src={this.svgs.mergeNextPageIcon}></img>
                  <div>
                    <p class='title'>{this.translator?.customPrint?.outputModeMergeNextPage}</p>
                    <p class='desc'>
                    {this.translator?.customPrint?.outputModeMergeNextPageDesc}
                    </p>
                  </div>
                </div>
              {/* </a> */}
            </li>
            <li onClick={() => {this.selectItem('continuous')}}>
              {/* <a class="option-wrapper"> */}
                <div class="file-option-item">
                  <img class="bubble__avatar-thumbnail" src={this.svgs.mergeContinuousIcon}></img>
                  <div>
                    <p class='title'>{this.translator?.customPrint?.outputModeMergeContinuous}</p>
                    <p class='desc'>
                      {this.translator?.customPrint?.outputModeMergeContinuousDesc}                 
                    </p>
                  </div>
                </div>
              {/* </a> */}
            </li>
          </ul>
          </calcite-combobox>
        </calcite-label>
        <slot></slot>
      </Host>
    );
  }

}
